#textHeader {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(45deg, #c771c783, #4e52da95);
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
}

#app {
    background: linear-gradient(135deg, #e439e4aa, #0e15e9aa), url("./bg.jpg");
    background-size: cover;
    background-position: center;
    height: 90%;
    width: 90%;
    border-radius: 30px;
    
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

#cromona {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 10%;
}

#textHeader {
    font-family: 'Raleway', sans-serif;
    font-size: 2.3rem;
    text-align: center;
    color: rgb(252, 243, 220);
    
}

h1 {
    margin: 0;
    font-weight: 800;
}

h2 {
    margin: 0;
    margin-bottom: 10px;
    font-size: 3rem;
    font-weight: 600;
}

h3 {
    font-weight: 400;
    margin: 0px;
    margin-bottom: 20px;
    font-size: 2.2rem;
}

p, a {
    font-size: 2rem;
    text-align: center;
    margin: 10px;
    color:  rgb(208, 201, 185);
}

.block {
    display: block;
    margin-bottom: 10px;
    height: 40px;
    width: 90%;
    font-size: 2.5em;
    height: 1.7em;
    box-sizing: border-box;
    padding: 10px 30px 10px 30px;
    outline: 0;
    border: 0px;
    background-color: #ffffff8c;
}

.refLink {
    color:  rgb(252, 243, 220);
}

.share {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: #ff8b8bb4;
    border-radius: 30px;
    margin: 20px;
    padding: 20px 30px;
}

.buttons {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    
    color: rgb(252, 243, 220);
}

.icons {
    font-size: 1.7em;
    width: 50%;
    height: 100%;
}

.toastCopy{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ff6060;
    padding: 20px;
    font-weight: 800;
    border-radius: 30px;
    top: 80vh;
    opacity: 0%;
}

.animate {
    animation: toast 2s ease-in-out;
}

.show {
    display: block;
}

.hidden {
    display: none;
}

.toastText {
    color: #2c2c2c;
}

@keyframes toast {
    0% {
        top: 72vh;
        opacity: 0%;
    }
    50% {
        top: 70vh;
        opacity: 100%;
    }
    100% {
        top: 68vh;
        opacity: 0%;
    }
}

button {
    height: 1.5em;
    width: 90%;
    font-size: 3em;
    font-weight: bold;
    color:  rgb(252, 243, 220);
    background-color: #ff6060;
    border: 0;
}

.block:focus {
    outline: 0px;
    border: 0px;
    background-color: #ffffffdc;
    animation: focusinput .5s;
}

@keyframes focusinput {
    from {background-color: #ffffff67;}
    to {background-color: #ffffffbb;}
  }

::placeholder {
    font-size: 3rem;
    color: #494949;
}

html, body {  
    margin: 0;
    font-family: 'Raleway', sans-serif;
    overflow: hidden;
}